import React,{useEffect, useState} from "react";
import "./SidebarOption.css";
import db, {timestamp} from './firebase'


function SidebarOptionNotification({ active, text, Icon,name,clearnoti }) {

  const [notification, setNotification]=useState([])
  const [noti, setNoti]=useState(false)

  useEffect(() => {
    db.collection('notifications').doc(name).collection('notification').onSnapshot((snapshot) =>
      setNotification(snapshot.docs.map((doc) =>(
        {
          id:doc.id,
          data: doc.data(),               
        }
      )))
    ); 
    console.log('this is: ', clearnoti)
  }, [notification]);
 
  return (
    <div className={`sidebarOption ${active && "sidebarOption--active"}`}>
      <h3> 
         <Icon size={25}/>
      </h3>

      <h2>{text}</h2>


    
        <p className={notification.length!==0 && clearnoti!==true?'notification-circle':'hide'}>{notification.length}</p>
       

      

    </div>
  );
}

export default SidebarOptionNotification;